'use client';
import { useEffect, useRef, useState } from 'react';
import Link from 'next/link';

export default function Page() {
  const oracleRef = useRef<HTMLDivElement>(null);
  const [lastDivination, setLastDivination] = useState<string | null>(null);

  useEffect(() => {
    // 高阶时空探查术
    async function advancedTimeSpaceDetection() {
      await new Promise<void>((resolve) => {
        setTimeout(function () {
          console.log('🌌 时空探查术第一层：感知虚空波动');
          resolve();
        }, 2000);
      });
    }
    
    advancedTimeSpaceDetection().then(
      result => console.log('⚡ 高阶时空探查完成，获得神识:', result)
    );
    console.log('🔮 时空探查术已启动，请稍候...');

    // 天外信息汲取·进阶版
    async function getDivination(owner: string, repo: string) {
      const celestialUrl = `https://api.github.com/repos/${owner}/${repo}`;
      try {
        const response = await fetch(celestialUrl);
        if (!response.ok) {
          throw new Error('天外通讯失败，灵力干扰');
        }
        const divineData = await response.json();
        // pushed_at 字段转化为天机预示
        return divineData.pushed_at as string;
      } catch (error) {
        console.error('🚨 天机探查遇阻:', error);
        return null;
      }
    }

    getDivination('yangjh-xbmu', 'Web-develop').then(prophecy => {
      console.log('🌟 最新天机预示:', prophecy);
      setLastDivination(prophecy);
    });
  }, []);

  return (
    <div className="flex flex-col min-h-screen text-slate-100" style={{
      background: 'linear-gradient(135deg, #0f172a 0%, #1e293b 40%, #334155 100%)'
    }}>
      <div className="absolute top-0 left-0 w-full h-full bg-grid-slate-700/[0.05] [mask-image:radial-gradient(ellipse_50%_50%_at_50%_50%,#000_60%,transparent_100%)]"></div>
      
      {/* 天机背景特效 */}
      <div className="absolute top-1/4 left-1/4 w-80 h-80 bg-gradient-to-r from-amber-500/10 to-transparent rounded-full blur-3xl animate-pulse"></div>
      <div className="absolute bottom-1/4 right-1/4 w-96 h-96 bg-gradient-to-r from-blue-600/8 to-transparent rounded-full blur-3xl animate-pulse" style={{ animationDelay: '1.5s' }}></div>
      
      <main className="flex-grow container mx-auto px-4 py-12 z-10">
        <div className="relative mb-12">
          <Link href="/" className="absolute -top-8 left-0 flex items-center gap-2 text-amber-400 hover:text-amber-300 transition-colors">
            <span className="text-2xl font-bold">←</span>
            <span>返回修真首页</span>
          </Link>
        </div>
        
        <section className="max-w-4xl mx-auto mb-10 rounded-xl border shadow-xl p-8" style={{
          background: 'rgba(15, 23, 42, 0.85)',
          borderColor: 'rgba(251, 191, 36, 0.3)',
          boxShadow: '0 25px 80px rgba(251, 191, 36, 0.1), inset 0 1px 0 rgba(251, 191, 36, 0.1)'
        }}>
          <h2 className="text-3xl font-bold mb-6" style={{
            color: '#fbbf24',
            textShadow: '0 0 15px rgba(251, 191, 36, 0.4)'
          }}>
            🔮 天机探查术·进阶
          </h2>
          <ul className="list-disc list-inside text-slate-300 space-y-3 mb-4 leading-relaxed">
            <li>此法门演示高阶时空探查：深层虚空感知、天外信息汲取、神识数据解析等进阶神通。</li>
            <li>修炼开始后会自动探查天外修真信息，获取最新的天机预示。</li>
            <li>请按 F12 开启神识感知界面，观察天机探查的运行轨迹。</li>
          </ul>
        </section>
        
        <section className="max-w-4xl mx-auto rounded-xl border shadow-2xl p-8 flex flex-col items-center" style={{
          background: 'rgba(15, 23, 42, 0.9)',
          borderColor: 'rgba(37, 99, 235, 0.3)',
          boxShadow: '0 25px 80px rgba(37, 99, 235, 0.1), inset 0 1px 0 rgba(37, 99, 235, 0.1)'
        }}>
          <h1 className="text-4xl font-bold mb-8 text-center" style={{
            background: 'linear-gradient(45deg, #fbbf24, #60a5fa, #a855f7)',
            backgroundClip: 'text',
            WebkitBackgroundClip: 'text',
            WebkitTextFillColor: 'transparent',
            textShadow: '0 0 30px rgba(251, 191, 36, 0.3)'
          }}>
            🌌 高阶时空探查演练
          </h1>
          
          <div className="mb-8 p-4 rounded-lg max-w-2xl text-center" style={{
            background: 'rgba(251, 191, 36, 0.1)',
            border: '1px solid rgba(251, 191, 36, 0.3)'
          }}>
            <p className="text-amber-200 font-medium">
              🔮 正在施展高阶天机探查术，汲取天外修真信息...
            </p>
          </div>
          
          <div
            id="oracle"
            ref={oracleRef}
            className="text-lg rounded-lg p-8 text-center w-full max-w-3xl transition-all duration-500"
            style={{
              background: 'rgba(15, 23, 42, 0.8)',
              border: '2px solid rgba(37, 99, 235, 0.4)',
              boxShadow: lastDivination ? '0 0 30px rgba(37, 99, 235, 0.3)' : 'none'
            }}
          >
            {lastDivination ? (
              <div>
                <div className="text-2xl mb-4 text-blue-400">📡 天机预示已获取</div>
                <div className="text-amber-300">最新天外修真记录时间:</div>
                <div className="text-xl font-mono mt-2 p-3 rounded" style={{
                  background: 'rgba(251, 191, 36, 0.1)',
                  border: '1px solid rgba(251, 191, 36, 0.3)',
                  color: '#fbbf24'
                }}>
                  {lastDivination}
                </div>
                <div className="text-sm text-slate-400 mt-4">
                  ✨ 此时间记录着天外修真界的最新动向
                </div>
              </div>
            ) : (
              <div className="text-slate-400">
                <div className="text-2xl mb-4">🌀</div>
                <div>施展天机探查术中...</div>
                <div className="text-sm mt-2">正在穿越虚空，汲取天外信息</div>
              </div>
            )}
          </div>
          
          <div className="mt-12 p-6 rounded-lg w-full max-w-4xl" style={{
            background: 'rgba(37, 99, 235, 0.1)',
            border: '1px solid rgba(37, 99, 235, 0.3)'
          }}>
            <h3 className="font-semibold mb-4 text-center" style={{ color: '#60a5fa' }}>
              🌟 天机探查进阶心法
            </h3>
            <div className="grid md:grid-cols-2 gap-6 text-sm text-slate-300">
              <div className="p-4 rounded-lg" style={{ background: 'rgba(15, 23, 42, 0.6)' }}>
                <div className="font-semibold text-amber-400 mb-2">🌌 深层虚空感知</div>
                <div>探测更深层次的时空维度，获取隐藏信息</div>
              </div>
              <div className="p-4 rounded-lg" style={{ background: 'rgba(15, 23, 42, 0.6)' }}>
                <div className="font-semibold text-amber-400 mb-2">📡 天外信息汲取</div>
                <div>跨越空间壁垒，汲取天外修真界的实时数据</div>
              </div>
              <div className="p-4 rounded-lg" style={{ background: 'rgba(15, 23, 42, 0.6)' }}>
                <div className="font-semibold text-amber-400 mb-2">🔮 神识数据解析</div>
                <div>运用高阶神识，解析和转化天外数据格式</div>
              </div>
              <div className="p-4 rounded-lg" style={{ background: 'rgba(15, 23, 42, 0.6)' }}>
                <div className="font-semibold text-amber-400 mb-2">⚡ 实时预示显现</div>
                <div>将获取的信息实时显现在物质世界中</div>
              </div>
            </div>
            
            <div className="mt-8 p-6 rounded-lg" style={{
              background: 'rgba(251, 191, 36, 0.1)',
              border: '1px solid rgba(251, 191, 36, 0.3)'
            }}>
              <h4 className="font-semibold mb-3 text-center" style={{ color: '#fbbf24' }}>
                💫 天机探查奥义
              </h4>
              <p className="text-slate-300 leading-relaxed text-center">
                &ldquo;天机玄妙，不可强求。修仙者若欲探查天机，需心境空明，神识纯净。
                高阶天机探查术不仅能汲取天外信息，更能预见未来之机缘与劫难。
                然切记：知天机者，当谨慎行事，切勿泄露天机，以免招致天罚。&rdquo;
              </p>
              <div className="text-center mt-4 text-amber-400 font-medium">
                —— 天机宗师·王林
              </div>
            </div>
          </div>
        </section>
      </main>
    </div>
  );
} 